import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PaginaEquipeAdministrativaComponent } from './pagina-equipe-administrativa.component';

const routes: Routes = [
  { path: '', component: PaginaEquipeAdministrativaComponent },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class PaginaEquipeAdministrativaRoutingModule { }
